.form__title {
  font-weight: 400;
  font-family: 'Inter', Arial, sans-serif;
  font-size: 50px;
  line-height: 1.2;
  text-transform: uppercase;
  margin: 90px 0 40px;
}

@media screen and (max-width: 1190px) {
  .form__title {
    font-size: 46px;
    margin: 80px 0 40px;
  }
}

@media screen and (max-width: 830px) {
  .form__title {
    font-size: 42px;
  }
}

@media screen and (max-width: 575px) {
  .form__title {
    margin: 60px 0 30px;
    font-size: 32px;
  }
}
